$body-background: null !default;

/*==============================================================================
  @body
==============================================================================*/

body {
  margin: 0;
  padding: 0;
  font-family: $font-family;
  font-size: $font-size;
  line-height: $line-height;
  font-weight: $font-weight;
  color: $color;
  -webkit-font-smoothing: subpixel-antialiased;
  background: $body-background;
}
